* {
  margin: 0;
  padding: 0;
  list-style: none;
}
.topnav {
  background-color: #333;
  overflow: hidden;
}
.topnav a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}
table,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
  margin-top: 50px;
}
.topnav a:hover {
  background-color: #ddd;
  color: black;
}
.topnav a.active {
  background-color: #4caf50;
  color: white;
}
.input_bar {
  position: relative;
  border: 3px solid #73ad21;
}
.fn-clear {
  overflow: hidden;
}
.fn-clear .fl {
  float: left;
}
.vediobox {
  max-width: 1200px;
  display: block;
  width: 100%;
  margin: auto;
}
.vediobox > .fl {
  margin: 30px auto;
  width: 23%;
}
.vediobox > .fl img {
  width: 80%;
}
.vediobox > .fl img:hover {
  box-shadow: 2px 2px 8px #666;
}
.pngbox {
  max-width: 1200px;
  display: block;
  width: 100%;
  margin: auto;
}
.pngbox > .fl {
  margin: 30px 0;
  width: 23%;
}
.pngbox > .fl img {
  width: 80%;
}
.pngbox > .fl img:hover {
  box-shadow: 2px 2px 8px #666;
}
.green {
  color: green;
}
.red {
  color: red;
}
@media screen and (max-width: 640px) {
  .vediobox {
    max-width: 1200px;
    display: block;
    width: 100%;
    margin: auto;
  }
  .vediobox > .fl {
    margin: 20px auto;
    width: 50%;
  }
  .vediobox > .fl img {
    width: 80%;
    display: block;
    margin: auto;
  }
  .pngbox {
    max-width: 1200px;
    display: block;
    width: 100%;
    margin: auto;
  }
  .pngbox > .fl {
    width: 50%;
  }
  .pngbox > .fl img {
    width: 80%;
    display: block;
    margin: auto;
  }
}
#shadowbox {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  left: 0;
  top: 0;
  bottom: 0;
  display: none;
}
#shadowbox iframe {
  max-width: 800px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  margin: 0;
  padding: 0;
}
#shadowbox > img {
  max-width: 800px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  margin: 0;
  padding: 0;
}
#top {
  position: fixed;
  right: 30px;
  bottom: 30px;
  width: 40px;
  height: 40px;
  display: none;
  border-radius: 50%;
  background-color: #f4f4f4;
}
#top svg {
  width: 30px;
  height: 30px;
  display: block;
  margin: 5px auto;
}
